  --dull_gray-1: #606060;
  --dull_gray-2: #707070;
  --yellow: #fe0;
  --white: #fff;
}
* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}
body {
  background-color: #fff;
  font-size: 0.83vw;
  line-height: 1;
  font-weight: normal;
  color: var(--almost_black);
  font-family: 'Myriad Pro', Arial, Helvetica, sans-serif;
}
a {
  cursor: pointer;
  text-decoration: none;
  font-weight: normal;
  line-height: 1;
}
