}
.btn {
  height: 4.75rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  line-height: 1;
  font-weight: 300;
}
.btn__large {
  max-width: 17.5rem;
}
.btn__medium {
  max-width: 13.75rem;
}
.btn__small {
  max-width: 10rem;
}
.btn__yellow {
  color: var(--dull_gray-2);
  background-color: var(--yellow);
  border: 1px solid var(--yellow);
}
.btn__yellow:hover {
  color: var(--almost_black);
  background-color: var(--white);
}
.btn__grey {
  color: var(--white);
  background-color: var(--dull_gray-2);
  border: 1px solid var(--dull_gray-2);
}
.btn__grey:hover {
  color: var(--almost_black);
  background-color: var(--white);
}
.btn__white {
  color: var(--dull_gray-2);
  border: 1px solid var(--white);
  background-color: var(--white);
}
.btn__white:hover {
  color: var(--dull_gray-2);
  background-color: var(--yellow);
  border: 1px solid var(--yellow);
}
.btn__black {
  color: var(--white);
  background-color: var(--black);
  border: 1px solid var(--black);
}
.btn__black:hover {
  color: var(--almost_black);
  background-color: var(--white);
}
@media screen and (max-width: 1023px) {
