.squares {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-bottom: 1.67vw;
  height: 100%;
}
.squares > :first-child {
  flex: 1;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.squares .bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.67vw 0 0;
}
.squares__item {
  width: calc(100% * 1 / 4);
  height: calc(100% * 1 / 3);
  position: relative;
  border: 1px solid #fff;
  background-color: #fff23b;
  overflow: hidden;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: 0;
  background-position: center center;
  transition: 0.25s ease-in-out;
  position: relative;
}
.squares__item:hover {
  transform: scale(0.9, 0.9);
  box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.75);
  border: 0px;
}
.squares__item:hover .squares__item__content {
  box-shadow: inset 0px 0px 8px 0px rgba(0,0,0,0.75);
}
.squares__item.show {
  background-size: cover;
}
.squares__item.show .squares__item__content {
  color: #fff;
}
.squares__item.show:before {
  content: '';
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #000;
  opacity: 0.6;
}
.squares__item img {
  display: none;
}
.squares__item:before {
  content: '';
  position: relative;
  display: block;
  width: 100%;
  padding-top: 85.31%;
}
.squares__item__content {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  padding: 2.5vw 2.5vw 1.25vw;
  z-index: 2;
  color: #161616;
  transition: 0.25s ease-in-out;
}
.squares__item__content .type {
  font-weight: 300;
  line-height: 1.2;
  margin-bottom: 1.56vw;
}
.squares__item__content .name {
  overflow: hidden;
  font-weight: 400;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  line-height: 1.3;
}
.squares__item__content .description {
  font-weight: 300;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
  line-height: 1.2;
  margin-top: 1.88vw;
}
