  p {
    font-size: 0.875rem;
    font-weight: normal;
    line-height: 1.2;
  }
  .runman {
    width: 10rem;
    top: 90%;
    right: -2.5%;
  }
  .scroll {
    display: none;
  }
  .bigText {
    font-size: 2.5rem;
    line-height: 1.1 !important;
  }
  .container {
    max-width: 100% !important;
    padding: 2.5vw;
  }
  .pageText {
    width: 80%;
  }
  .pageText h1 {
    font-size: 36px;
  }
  .pageText .theme {
    font-size: 30px;
    line-height: 1;
    margin-bottom: 2rem !important;
  }
  .mock-up .gallery,
  .draft-blank .gallery {
    column-count: 2;
  }
  .squares__item {
    width: calc(100% * 1 / 2);
    min-width: 320px;
  }
  .squares__item__content {
    padding: 0.875rem;
  }
  .squares__item:before {
    padding-top: 90%;
  }
}
